    </v-row>
    <v-row>
      <!-- Component with buttons -->
      <v-col cols="12" align="center">
        <media-buttons></media-buttons>
      </v-col>
    </v-row>
    <v-row class="footer">
      <v-col cols="12" align="center">
        Still under construction old page
        <a href="https://www.csd.uoc.gr/~csd3142" target="_blank">
          <span class="here">Here</span>
