<script>
import mediaButtons from "./media-buttons";

export default {
  name: "home",
  components: {
    mediaButtons
  },
  mounted() {
    this.setBackGround();
  },
  methods: {
    setBackGround: () => {
      let canvas = document.getElementById("matrix");
      canvas.height = window.innerHeight;
      canvas.width = window.innerWidth;
      let canvasContext = canvas.getContext("2d");
      let characters = "qwertyuiopasdfghjkl1234567890zxcvbnm";

      var font_size = 10;
      var columns = canvas.width / font_size;
      var drops = [];

      for (var x = 0; x < columns; x++) drops[x] = 1;

      function draw() {
        canvasContext.fillStyle = "rgba(0, 0, 0, 0.05)";
        canvasContext.fillRect(0, 0, canvas.width, canvas.height);

        canvasContext.fillStyle = "#0F0";
        canvasContext.font = font_size + "px arial";
        //looping over drops
        for (var i = 0; i < drops.length; i++) {
          var text = characters[Math.floor(Math.random() * characters.length)];

          canvasContext.fillText(text, i * font_size, drops[i] * font_size);

          if (drops[i] * font_size > canvas.height && Math.random() > 0.975)
            drops[i] = 0;

          //incrementing Y coordinate
          drops[i]++;
        }
      }

      setInterval(draw, 33);
    }
  }
};
</script>

